<template>
    <div class="public_content">
        <div class="public_title"><span>小区查看</span></div>
        <ul class="lists_wrap">
            <li v-for="(item,index) in lists" :key="index" class="item" @click="">
                <p>
                    <!-- <span class="user">{{item.createuser}}</span> -->
                    <span class="user">创建时间</span>
                    <span class="time">{{item.createtime | formatDate}}</span>
                </p>

                <div class="detail">
                    <img class="thumb" :src="item.thumb" />
                    <div class="info">
                        <p class="name">{{item.communityname}}</p>
                        <p>
                          <span class="avgprice">{{item.avgprice ? item.avgprice : '面议'}}
                                <span v-show="item.avgprice" style='color: #333;font-weight: 400;font-size: 14px;'>元/月</span></span>
                            <span class="address">
                                 {{item.region_name}}{{item.estate_name}}{{item.address}}
                            </span> 
                        </p>
                    </div>

                    <span class="status" :class="Number(item.status) ? 'orange' : 'green'">{{Number(item.status) ? '已审核' : '待审核'}}</span>

                    <!-- <div class="num">
                        <i class="iconfont">&#xe64f;</i>
                        <span>{{item.num}}</span>
                    </div> -->
                </div>
            </li>
        </ul>

        <!-- 分页 -->
        <div class="mt-20 text-right">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page.sync="page.currentPage3"
            :page-size="page.size"
            layout="prev, pager, next, jumper"
            :total="page.total">
          </el-pagination>          
        </div>
    </div>
</template>

<script>
import { getCommunityList } from "@/api/house";
export default {
  data() {
    return {
      page: {
        total: null,
        pageNo: 1,
        pageSize: 10
      },
      lists: []
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      getCommunityList({
        pageNo: this.page.pageNo,
        pageSize: this.page.pageSize
      }).then(res => {
        if (res.data.code == 1) {
          this.page.total = res.data.total;
          this.lists = res.data.data;
        }
      });
    },
    handleCurrentChange(val) {
      this.page.pageNo = val;
      this.fetchData();
    }
  }
};
</script>

<style lang='scss' scoped>
</style>



